import {
  Card, CardBody, CardFooter, CardGroup, CardHeader,
} from '@trussworks/react-uswds';
import { SHOW_STEPS } from '../core';
import { IStep } from '../survey';
import { WizardNavbar } from './WizardNavbar';

/**
 * Generates the Card layout for each step's contents
 * @param props
 * @returns
 */
export const StepLayout = (props: IStep): JSX.Element => {
  const getHeader = () => {
    let text = props.question.questionText;
    if (!text) return <></>;

    if (SHOW_STEPS) {
      text = `${props.question.id}: ${text}`;
    }
    return <h3 className="usa-card__heading">{text}</h3>;
  };

  const getSupportingDetails = () => {
    const text = props.question.supportingDetails;
    if (!text) return <></>;
    return <p className="font-sans-6">{text}</p>;
  };

  const getQuestionHelp = () => {
    const text = props.question.questionHelp;
    if (!text) return <></>;
    return <p className="font-sans-6">{text}</p>;
  };

  const getFooter = () => {
    const text = props.question.footer;
    if (!text) return <></>;
    return <p className="font-sans-6">{text}</p>;
  };

  return (
    <div>
      <section>
        <CardGroup>
          <Card
            headerFirst
            gridLayout={{ tablet: { col: 12 } }}
            containerProps={{ className: 'border-ink' }}
          >
            <CardHeader className="bg-base-lightest">
              {getHeader()}
              {getSupportingDetails()}
              {getQuestionHelp()}
            </CardHeader>
            <CardBody className="padding-top-3">{props.children}</CardBody>
            <CardFooter>
              {getFooter()}
            </CardFooter>
          </Card>
        </CardGroup>
      </section>
      <WizardNavbar {...props} />
    </div>
  );
};
