import { Accordion, Button } from '@trussworks/react-uswds';
import { IS_DEV } from '../core';
import { IPrepStep } from '../survey';

export const WizardDevPanel = (props: IPrepStep): JSX.Element => {
  if (!IS_DEV) return <></>;

  const reset = () => {
    props.dispatchForm({
      type: 'RESET',
    });
    return props.wizard.goToStep('A');
  };

  return (
    <>
      <Accordion
        items={[
          {
            title: 'Temporary developer panel',
            content: (
              <pre>
                <code>{JSON.stringify(props.form, null, 4)}</code>
              </pre>
            ),
            expanded: false,
            id: 'developer-output',
          },
        ]}
      />
      <br></br>
      <nav className="wizard-layout__navbar">
        <Button type="reset" secondary onClick={reset}>
          Reset
        </Button>
      </nav>
    </>
  );
};
