import { Link, SiteAlert } from '@trussworks/react-uswds';
import { questionnaire } from '../../flows/ssa_eligibility/questionnaire';
import { IStep } from '../../survey/IStep';
import { StepLayout } from '../StepLayout';

/**
 * Displays the wizard results
 * @param props
 * @returns
 */
export const NoResults = (props: IStep): JSX.Element => {
  const { question } = props;
  if (!question) {
    return <></>;
  }

  const action = questionnaire.getAction();

  return (
    <StepLayout {...props}>
      <SiteAlert variant="emergency">
        <p className="usa-alert__text">
          <strong>{action.name}</strong>:&nbsp;
          {action.description}&nbsp;
          <Link href="#">{action.action}</Link>.
        </p>
      </SiteAlert>
    </StepLayout>
  );
};
