import { STEP } from '../core';
import { questionnaire } from '../flows/ssa_eligibility/questionnaire';
import { IPrepStep } from '../survey';
import {
  DateOfBirth,
  Landing,
  MultipleChoice,
  NoResults,
  Results,
  Summary,
} from './steps';

export const StepFactory = (props: IPrepStep): JSX.Element => {
  const { step } = props;
  const question = questionnaire.getQuestionById(`${step}`);
  switch (question.questionType) {
    case STEP.LANDING_STEP:
      return <Landing {...{ question, ...props }} />;
    case 'DoB':
      return <DateOfBirth {...{ question, ...props }} />;
    case 'multipleChoice':
      return <MultipleChoice {...{ question, ...props }} />;
    case STEP.NO_RESULTS_STEP:
      return <NoResults {...{ question, ...props }} />;
    case STEP.RESULTS_STEP:
      return <Results {...{ question, ...props }} />;
    case STEP.SUMMARY_STEP:
      return <Summary {...{ question, ...props }} />;
    default:
      return <>Step does not exist</>;
  }
};
