import { Link, SiteAlert, SummaryBox } from '@trussworks/react-uswds';
import { ReactNode } from 'react';
import { IS_DEV } from '../../core';
import { questionnaire } from '../../flows/ssa_eligibility/questionnaire';
import { IResult } from '../../survey/IResult';
import { IStep } from '../../survey/IStep';
import { StepLayout } from '../StepLayout';

/**
 * Internal method to compute reason for a result
 * @param props
 * @param result
 * @returns
 */
const getReason = (props: IStep, result: IResult): string => {
  let reason = result.match?.explanation;
  if (!reason) {
    return '';
  }

  if (IS_DEV && result.match) {
    reason += '<br><br>';
    if (
      result.match.ageCalc !== undefined
      || result.match.minAge !== undefined
      || result.match.maxAge !== undefined
    ) {
      reason += `You are ${props.form.age?.years} years and ${props.form.age?.months} months old. `;
    }
    Object.keys(result.match.answers).forEach((id) => {
      const q = questionnaire.getQuestionById(id);
      reason += `You answered "<b>${q.answer}</b>" to the question "<i>${q.questionText}.</i>" `;
    });
  }
  return reason;
};

/**
 * Internal method to generate list of results
 * @param props
 * @returns
 */
const getResults = (props: IStep): ReactNode =>
  questionnaire.getResults(props.form).map((result) => (
    <li key={`${props.step}_${result.code}`} className="padding-bottom-2">
      <span>
        Benefit name:&nbsp;&nbsp;
        <b>{result.description}</b>
      </span>
      <div
        className="text-light"
        dangerouslySetInnerHTML={{ __html: getReason(props, result) }}
      />
    </li>
  ));

/**
 * Displays the wizard results
 * @param props
 * @returns
 */
export const Results = (props: IStep): JSX.Element => {
  const { question } = props;
  if (!question) {
    return <></>;
  }

  const action = questionnaire.getAction();

  return (
    <StepLayout {...props}>
      <SummaryBox heading="">
        <ul
          className="usa-list usa-list--unstyled"
          style={{ textAlign: 'left' }}
        >
          {getResults(props)}
        </ul>
        <SiteAlert variant="info">
          <p className="usa-alert__text">
            <strong>{action.name}</strong>:&nbsp;
            {action.description}&nbsp;
            <Link href="#">{action.action}</Link>.
          </p>
        </SiteAlert>
      </SummaryBox>
    </StepLayout>
  );
};
