const MODE = process.env.NODE_ENV;

const qs = new URLSearchParams(window.location.search);

// TODO: export a configuration class and instance

// We're in dev mode if running locally OR if `dev` QS param says so
export const IS_DEV = MODE !== 'production'
  || (qs.has('dev') && qs.get('dev') === 'true');

// We're in dev mode if running locally OR if `dev` QS param says so
export const SHOW_STEPS = IS_DEV
  || (qs.has('showSteps') && qs.get('showSteps') === 'true');
