import { Fieldset, Radio } from '@trussworks/react-uswds';
import { kebabCase } from 'lodash';
import { ReactNode } from 'react';
import { Answer } from '../../survey/Answer';
import { IStep } from '../../survey/IStep';
import { StepLayout } from '../StepLayout';

const updateForm = (answer: string, props: IStep): void => {
  Object.assign(props.question, { answer });
  const value = {
    answers: {
      [props.question.id]: props.question,
    },
  };
  return props.dispatchForm({
    type: 'UPDATE',
    value,
  });
};

const getSelected = (answer: string, props: IStep): boolean | undefined => {
  if (!props || !props.form) return undefined;
  return Answer.isSelected(props.form, props.question.id, answer);
};

const getFieldSetName = (props: IStep): string => kebabCase(props.question.questionText);

const getDomId = (answer: string, props: IStep): string => {
  const name = getFieldSetName(props);
  return `${name}-${kebabCase(answer)}`;
};

const getRadio = (answer: string, props: IStep): ReactNode => {
  const handler = () => updateForm(answer, props);
  const id = getDomId(answer, props);

  return (
    <Radio
      id={id}
      key={id}
      name={getFieldSetName(props)}
      label={answer}
      value={answer}
      checked={getSelected(answer, props) === true}
      className={'multipleChoice'}
      onChange={handler}
      onClick={handler}
    />
  );
};

const getRadios = (props: IStep): ReactNode => {
  const ret: ReactNode[] = [];
  Object.keys(props.question.answers).forEach((a) => {
    ret.push(getRadio(props.question.answers[+a], props));
  });
  return ret;
};

/**
 * Renders a question and a radio list of allowed answers
 * @param props
 * @returns
 */
export const MultipleChoice = (props: IStep): JSX.Element => {
  if (props?.question?.answers === undefined) {
    return <></>;
  }

  return (
    <StepLayout {...props}>
      <Fieldset
        legend={props.question.questionText}
        className="multipleChoice"
        legendStyle="srOnly"
      >
        {getRadios(props)}
      </Fieldset>
    </StepLayout>
  );
};
