import { Button } from '@trussworks/react-uswds';
import { STEP } from '../core';
import { questionnaire } from '../flows/ssa_eligibility/questionnaire';
import { IStep } from '../survey/IStep';
import { Questionnaire } from '../survey/Questionnaire';

/**
 * Generates the Previous/Next buttons for Wizard navigation
 * @param props
 * @returns
 */
export const WizardNavbar = (props: IStep): JSX.Element => {
  // We can go back only once past the Landing step and the first actual step.
  // Once past Summary, back is disabled
  const showPrevStep = props.step !== STEP.LANDING_STEP
    && props.step !== STEP.RESULTS_STEP
    && props.step !== STEP.NO_RESULTS_STEP
    && props.step !== questionnaire.flow[1];
  // Results will be the last step; nothing follows
  const showNextStep = props.step !== STEP.RESULTS_STEP
    && props.step !== STEP.NO_RESULTS_STEP;

  const backLabel = 'Previous';
  // TODO: update this logic to be generic see: https://github.com/usds/ssa-eligibility/issues/57
  let nextLabel = '';
  switch (props.step) {
    case STEP.LANDING_STEP:
      nextLabel = 'Get Started';
      break;
    case STEP.SUMMARY_STEP:
      nextLabel = 'Submit';
      break;
    default:
      nextLabel = 'Next';
      break;
  }

  const goToStep = (step: string) => props.wizard.goToStep(step);

  const next = () => goToStep(questionnaire.getNextStep(props));

  const prev = () => goToStep(questionnaire.getPreviousStep(props));

  const disabled = () => !Questionnaire.isNextEnabled(props);

  return (
    <nav className="wizard-layout__navbar">
      {/* Previous step */}
      {showPrevStep && (
        <Button
          type="button"
          outline
          onClick={prev}
          data-testid={`prev-button-${props.step}`}
        >
          {backLabel}
        </Button>
      )}
      {!showPrevStep && <div></div>}

      {/* Forward step */}
      {showNextStep && (
        <Button
          type="button"
          onClick={next}
          disabled={disabled()}
          data-testid={`next-button-${props.step}`}
        >
          {nextLabel}
        </Button>
      )}
      {!showNextStep && <div></div>}
    </nav>
  );
};
