import { Header, Title } from '@trussworks/react-uswds';
import { useReducer } from 'react';
import { useWizard } from 'use-wizard';
import { questionnaire } from '../flows/ssa_eligibility/questionnaire';
import { formReducer } from '../state/formReducer';
import { Answer } from '../survey/Answer';
import { StepFactory } from './StepFactory';
import { WizardDevPanel } from './WizardDevPanel';
import { WizardProgressBar } from './WizardProgressBar';

export const EligibilityWizard = (): JSX.Element => {
  const [step, wizard] = useWizard(questionnaire.flow);

  const results = new Answer();
  // This is only used to store user inputs
  const [form, dispatchForm] = useReducer(formReducer, results);
  return (
    <>
      <Header basic>
        <div className="usa-nav-container">
          <div className="usa-navbar">
            <Title id="basic-logo">
              <a href="/" title="Home" aria-label="Home">
                {questionnaire.header}
              </a>
            </Title>
          </div>
        </div>
      </Header>

      <section>
        <WizardProgressBar
          {...{
            step,
            wizard,
            form,
            dispatchForm,
          }}
        ></WizardProgressBar>
      </section>

      <section className="section">
        <StepFactory
          {...{
            step,
            wizard,
            form,
            dispatchForm,
          }}
        />
      </section>

      <section className="section">
        <WizardDevPanel
          {...{
            step,
            wizard,
            form,
            dispatchForm,
          }}
        />
      </section>
    </>
  );
};
